.live-btn {
  display: inline-block;
  height: 30px;
  padding: 0 20px;
  border: none;
  box-sizing: border-box;
  vertical-align: middle;
  font-size: 12px;
  font-family: "Microsoft YaHei", "Microsoft Sans Serif", "Microsoft SanSerf", "微软雅黑";
  line-height: 30px;
  text-align: center;
  text-decoration: none;
  white-space: nowrap;
  border-radius: 5px;
  outline: none;
  cursor: pointer;
  overflow: visible;
  -webkit-transition: all cubic-bezier(0.22, 0.58, 0.12, 0.98) 0.2s;
  transition: all cubic-bezier(0.22, 0.58, 0.12, 0.98) 0.2s;
}
.live-btn:active {
  -webkit-transition: none;
  transition: none;
}
.live-btn.default {
  color: #fff;
  background: #4fc1e9;
}
.live-btn.default:hover {
  background: #58d4ff;
}
.live-btn.default:active {
  background: #4aace5;
}
.live-btn.default.pink {
  background-color: #fb7299;
}
.live-btn.default.pink:hover {
  background-color: #fb80a3;
}
.live-btn.default.pink:active {
  background-color: #fd8481;
}
.live-btn.default.cancel {
  color: #64a2c5;
  background: #eaf8ff;
}
.live-btn.default.cancel:hover {
  color: #58d4ff;
  background: #e2fbff;
}
.live-btn.default.cancel:active {
  color: #64a2c5;
  background: #d6edff;
}
.live-btn.default.disabled {
  background-color: #f2f2f2 !important;
  color: #999 !important;
  cursor: not-allowed;
}
.live-btn.ghost {
  background-color: transparent;
  border: 1px solid #4fc1e9;
  color: #4fc1e9;
}
.live-btn.ghost:hover,
.live-btn.ghost.active {
  background-color: #4fc1e9;
  color: #fff;
}
.live-btn.ghost:active {
  background-color: #4aace5;
  color: #fff;
}
.live-btn.ghost.disabled {
  background-color: transparent !important;
  border-color: $gray-light;
}
.live-btn.ghost.pink {
  color: #e74e8f;
  border-color: #e74e8f;
}
.live-btn.ghost.pink:hover {
  background-color: transparent !important;
  border-color: #ef83b0;
  color: #ef83b0;
}
.live-btn.ghost.pink:active {
  background-color: #ef83b0 !important;
  border-color: #ef83b0;
  color: #fff;
}
.live-btn.ghost.green {
  color: $biliGreen;
  border-color: $biliGreen;
}
.live-btn.ghost.green:hover {
  background-color: transparent !important;
  border-color: #95ce72;
  color: #95ce72;
}
.live-btn.ghost.green:active {
  background-color: #95ce72 !important;
  border-color: #95ce72;
  color: #fff;
}
