.top-nav-btn {
  height: 25px;
  padding: 0 7px;
  margin: 12.5px 2px;
  border-radius: 5px;
  color: #646c7a;
  line-height: 25px;
  cursor: pointer;
}
.top-nav-btn:hover {
  color: #4fc1e9;
}
.top-nav-btn:active {
  background-color: #4fc1e9;
  color: #fff;
}
.nav-btn-divider {
  background-color: #646c7a;
  width: 1px;
  height: 15px;
  margin: 17.5px 0;
}
.user-live-ctnr {
  margin-top: 11px;
  width: 122px;
  height: 32px;
  box-sizing: border-box;
  box-shadow: 0px 2px 5px rgba(34,23,21,0);
  overflow: hidden;
  background-color: #fff;
  border-radius: 2px;
}
.user-live-ctnr:hover {
  height: 64px;
  -webkit-transition: all ease-in 0.3s;
  transition: all ease-in 0.3s;
  box-shadow: 0px 2px 5px rgba(34,23,21,0.2);
}
.user-live-ctnr:hover .room-enter-btn {
  color: #999;
}
.user-live-ctnr:hover .room-enter-btn:hover {
  color: #23aee6;
}


